// IMPORT
@import url('https://fonts.googleapis.com/css?family=Muli:300,400,700,900');

// Variables
$mainC: #f1f3f6;
$secC: #2979ff;
$svgC: rgba(#929cd0, .8);
$sz: 300px;

// Placeholders
%center {
  display: flex;
  justify-content: center;
  align-items: center;
}

// HTML
html {
  background: $mainC;
  font-family: 'Muli',sans-serif;
  color: rgba(51,51,51,1);
}

h1 {
    font-size: 2.0em;
    font-weight: 800;
    padding: 4px;
    margin: 6px;
}



// SVGs
#dribbble {
  position: fixed;
  display: block;
  right: 70px;
  bottom: 16px;
  svg {
    display: block;
    width: 76px;
    height: 24px;
    fill: $svgC;
  }
}

// Main Structure
.App {
    text-align: center;
    padding: 18px;
    margin: 18px;
    border-radius: 12px;
    box-shadow: 
    inset 6px 6px 12px #b7b9bb,
    inset -6px -6px 12px #ffffff;
  }

.header {
    // height: 72px;
    padding: 1px;
    margin: 12px;
    color: rgba(0, 0, 0, .65);
    // font-size: 1.5em;
    // font-weight: 800;
    text-align: center;
    justify-content: center;
    align-items: center;
    text-shadow: 0 2px 2px rgba(0, 0, 0, .2);
    border-radius: 12px;
    background: $mainC;
    box-shadow: 
    inset 0 0 15px rgba(55, 84, 170,0),
    inset 0 0 20px rgba(255, 255, 255,0),
    7px 7px 15px rgba(55, 84, 170,.15),
    -7px -7px 20px rgba(255, 255, 255,1),
    inset 0px 0px 4px rgba(255, 255, 255,.2);
    p {
        margin-top: 2em;
        line-height: 1.45em;
        padding: 4px;
        margin: 3px;
        color: rgba(0, 0, 0, .45);
      }
}

.container {
  width: 100%;
  height: auto;
  // border: 1px solid black;
  padding: 16px;
  margin: auto;
  border-radius: 12px;
  background: $mainC;
  display: flex;
  flex-wrap: wrap;
  @extend %center; 
}

.box {
  @extend %center; 
  flex-direction: column;
  width: $sz*1.2;
  height: $sz*1.4;
  padding: auto;
  margin: 42px;
  // border: 1px solid #fff;
  background: $mainC;
  border-radius: 20px;
  box-shadow: 
    inset 0 0 15px rgba(55, 84, 170,0),
    inset 0 0 20px rgba(255, 255, 255,0),
    7px 7px 15px rgba(55, 84, 170,.15),
    -7px -7px 20px rgba(255, 255, 255,1),
    inset 0px 0px 4px rgba(255, 255, 255,.2);
}

.text {
  width: 80%;
//   height: auto;
  color: rgba(0, 0, 0, .65);
  text-shadow: 0 2px 2px rgba(0, 0, 0, .2);
  h1 {
    font-size: 1.2em;
    font-weight: 800;
  }
  h2 {
    font-size: 1.5em;
    font-weight: 600;
  }
  p {
    margin-top: 2em;
    line-height: 1.45em;
    padding: 0px;
    margin: 1px;
  }
}

.btn {
  margin-top: 2em;
  margin-bottom: 2em;
  display: flex;
  flex-direction: column;
  .cta {
    width: 100px;
    height: 33px;
    text-decoration: none;
    background: $secC;
    color: #fff;
    padding: 10px 63px;
    border-radius: 25px;
    font-weight: 600;
    box-shadow: 0px 10px 15px rgba(55, 84, 170,.25);
  }
}

// Tooltip
.tooltip {
  position: relative;
}

.tooltip .tooltiptext {
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  width: 120px;
  background-color: rgba(0, 0, 0, .4);
  color: #fff;
  text-align: center;
  border-radius: 25px;
  padding: 5px 0 6px 0;
  position: absolute;
  top: -40px;
  left: calc(50% - 60px);
  z-index: 1;
  font-size: .7em;
  opacity: 0;
  box-sizing: border-box;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
  transition: opacity 399ms ease;
}